import React, { Fragment } from 'react';
import ReviewStarPanel from '../../../../commonComponents/ReviewStarPanel';
import { Observer } from 'mobx-react';
import Card from '../../../../commonComponents/Card';

//import { toJS } from 'mobx';

class Page1Card3 extends React.Component {

    componentDidMount() {
       //console.log('Page1Card3 : ',this)
       //console.log('Page1Card3 -props : ',toJS(this.props.data));
    }

    render(){
        const lang = this.props.lang;
        return (
        		<Card
  title={this.props.data.attributes.title?this.props.data.attributes.title[lang]:false} seeall={this.props.data.attributes.seeall?this.props.data.attributes.seeall[lang]:false} seeAllUrl={this.props.data.attributes.seeAllUrl}
  baseUrl={typeof this.props.data.attributes.baseUrl === "string" && this.props.data.attributes.baseUrl.length>0 ? this.props.data.attributes.baseUrl : false}
  seeAllPageTitle={this.props.data.attributes.seeAllPageTitle}
  sectionTitle={this.props.data.attributes.sectionTitle}
  sectionId={this.props.data.attributes.sectionId}
  topicId={this.props.data.attributes.topicId}
  subTopicId={this.props.data.attributes.subTopicId}
  deeplink={this.props.data.attributes.deeplink}
  titleId={this.props.data.attributes.titleId}
>
<Observer>{()=>{return(<ReviewStarPanel starClassName={this.props.data.children[0].attributes.starClassName} reviews={this.props.data.children[0].attributes.reviews}
dhPostId={this.props.data.children[0].attributes.dhPostId}
/>
)}}</Observer>

</Card>

        )
    }
}

export default Page1Card3;
