import React, { Component, Fragment } from 'react';

import PropTypes from 'prop-types';
import { inject, observer} from 'mobx-react';
import AppStore from '../../store/AppStore1';
import loadable from '@loadable/component';
import { LazyRenderer, EmptyComponent } from './../../utility/lazyRenderer';

import Page1Card1 from './subComponent/page1/page1card1';
import Page1Card2 from './subComponent/page1/page1card2';
import Page1Card3 from './subComponent/page1/page1card3';
import Page1Card4 from './subComponent/page1/page1card4';
import Page1Card5 from './subComponent/page1/page1card5';
import Page1Card6 from './subComponent/page1/page1card6';
import Page1Card7 from './subComponent/page1/page1card7';
import Page1Card8 from './subComponent/page1/page1card8';
import Page1Card9 from './subComponent/page1/page1card9';
import Page1Card10 from './subComponent/page1/page1card10';
import Page1Card11 from './subComponent/page1/page1card11';



@inject('appStore')
@observer
class LandingPage1 extends Component {
  constructor(props) {
    super(props);
}

static propTypes = {
    appStore: PropTypes.object.isRequired,
};

static defaultProps = {
    appStore: new AppStore(),
};

  componentDidMount(){
    if(typeof(landingPageDidMount)!=="undefined"){
      landingPageDidMount=true;
    }
  }
  
  render() {
    const { data } = this.props.appStore;
    return (
      <div>
        {!data.cards[0].loadError && <Page1Card1 data={data.cards[0]} lang={data.lang} appStore={this.props.appStore}></Page1Card1>}
{!data.cards[1].loadError && <Page1Card2 data={data.cards[1]} lang={data.lang} appStore={this.props.appStore}></Page1Card2>}
{!data.cards[2].loadError && <Page1Card3 data={data.cards[2]} lang={data.lang} appStore={this.props.appStore}></Page1Card3>}
{!data.cards[3].loadError && <Page1Card4 data={data.cards[3]} lang={data.lang} appStore={this.props.appStore}></Page1Card4>}
{!data.cards[4].loadError && <Page1Card5 data={data.cards[4]} lang={data.lang} appStore={this.props.appStore}></Page1Card5>}
{!data.cards[5].loadError && <Page1Card6 data={data.cards[5]} lang={data.lang} appStore={this.props.appStore}></Page1Card6>}
{!data.cards[6].loadError && <Page1Card7 data={data.cards[6]} lang={data.lang} appStore={this.props.appStore}></Page1Card7>}
{!data.cards[7].loadError && <Page1Card8 data={data.cards[7]} lang={data.lang} appStore={this.props.appStore}></Page1Card8>}
{!data.cards[8].loadError && <Page1Card9 data={data.cards[8]} lang={data.lang} appStore={this.props.appStore}></Page1Card9>}
{!data.cards[9].loadError && <Page1Card10 data={data.cards[9]} lang={data.lang} appStore={this.props.appStore}></Page1Card10>}
{!data.cards[10].loadError && <Page1Card11 data={data.cards[10]} lang={data.lang} appStore={this.props.appStore}></Page1Card11>}

      </div>
    )
  }
}

export default LandingPage1;
